
import { axios } from '@/router/config';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import vheader from '@/components/header/singinHeader';
export default {
components: {
vheader,
Group,
XInput,
XButton,
Alert
},
data () {
return {
validCode: '', //验证码
captchas: '',
email: '',
valid3: '',
valid6: '',
content: '',
show2: false
};
},
created () {
this.getCaptchas();
},
methods: {
getValid3 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
self.valid3 = patt.test(self.email) ? ' ' : '请填写有效的Email';
if (self.valid3 === ' ') {
resolve();
}
});
},
getValid6 () {
var self = this;
return new Promise(function (resolve, reject) {
self.valid6 = self.validCode ? ' ' : '请填写有效的验证码';
if (self.valid6 === ' ') {
resolve();
}
});
},
getCaptchas () {
var self = this;
axios('get', '/getCaptchas', {}, data => {
if (data.code === 200) {
self.captchas = data.data;
}
});
},
findPass () {
var self = this;
Promise.all([this.getValid3(), this.getValid6()]).then(function (val) {
axios('get', '/findPass',
{
email: self.email,
validCode: self.validCode
}, data => {
if (data.status === 200) {
console.log('success');
} else {

self.content = data.message;
}
});
self.show2 = true;
self.content = '已发送您邮箱';
});
},
onHide () {
this.$router.push(`/login`);
}
}
};



