
import { axios } from '@/router/config';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import vheader from '@/components/header/singinHeader';
export default {
components: {
vheader,
XInput,
XButton,
Group,
CheckIcon,
Cell,
Alert
},
data () {
return {
account: '', //账号
valid1: '',
nickname: '', //昵称
valid2: '',
email: '', //邮箱
valid3: '',
password: '',
valid4: '',
rpwd: '',
valid5: '',
validCode: '', //验证码
valid6: '',
captchas: '',
show2: false,
title: '',
content: ''
};
},
created () {
this.getCaptchas();
},
methods: {
getValid1 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^[a-zA-Z0-9]{4,16}$/;
self.valid1 = patt.test(self.account) ? ' ' : '必须是4到16位字母或数字组成';
if (self.valid1 === ' ') {
resolve();
}
});
},
getValid2 () {
var self = this;
return new Promise(function (resolve, reject) {
self.valid2 = self.nickname ? ' ' : '请填写有效的昵称';
if (self.valid2 === ' ') {
resolve();
}
});
},
getValid3 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(.[a-zA-Z0-9_-])+/;
self.valid3 = patt.test(self.email) ? ' ' : '请填写有效的Email';
if (self.valid3 === ' ') {
resolve();
}
});
},
getValid4 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^[a-zA-Z0-9]{4,16}$/;
self.valid4 = patt.test(self.password) ? ' ' : '必须是4到16位字母或数字组成';
if (self.valid4 === ' ') {
resolve();
}
});
},
getValid5 () {
var self = this;
return new Promise(function (resolve, reject) {
self.valid5 = self.password === self.rpwd ? ' ' : '密码不一致  ';
if (self.valid5 === ' ') {
console.log(5);
resolve();
}
});
},
getValid6 () {
var self = this;
return new Promise(function (resolve, reject) {
self.valid6 = self.validCode ? ' ' : '请填写有效的验证码';
if (self.valid6 === ' ') {
resolve();
}
});
},
getCaptchas () {
var self = this;
axios('get', '/getCaptchas', {}, data => {
if (data.code === 200) {
self.captchas = data.data;
}
});
},
submit () {
let self = this;
Promise.all([this.getValid1(), this.getValid2(), this.getValid3(), this.getValid4(), this.getValid5(), this.getValid6()]).then(function (val) {
axios('get', '/register',
{
account: self.account,
nickname: self.nickname,
email: self.email,
password: self.password,
validCode: self.validCode
}, data => {
self.show2 = true;
if (data.status === 200) {

self.content = '账号注册成功，请到填写的邮箱激活，即可立即登陆';
self.$router.push(`/needActive?a=${data.data.account}`);
} else {

self.content = data.message;
}
});
});
}
}
};



