
const ERROR_OK = 200;
import { axios } from '@/router/config';
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import Alert from 'vux/src/components/alert/index.vue'
import value2name from 'vux/src/filters/value2name.js'

export default {
data () {
return {
nickname: '',
birthday: '',
sex: '',
address: [], //北京市东城区
ChinaAddressV3Data: ChinaAddressV3Data,
show2: false,
content: '',
style: {
'z-index': 2000
}
};
},
components: {
Group,
Checker,
CheckerItem,
XInput,
XButton,
Datetime,
XAddress,
Alert,
ChinaAddressV3Data
},
created () {
this.getUserMessage();
},
methods: {
getUserMessage () {
var self = this;
axios('get', '/getUserMessage', {}, data => {
if (data.status === ERROR_OK) {
self.nickname = data.data.nickname;
self.sex = Number(data.data.sex);
self.birthday = data.data.birthday;
self.address = [data.data.province, data.data.city, data.data.area];
}
});
},
setMessage () {
const self = this;
if (self.nickname === '' || self.nickname === undefined || self.nickname === null) {
self.show2 = true;
self.content = '用户名不能为空';
} else {
axios('get', '/updateUserMessage',
{
nickname: self.nickname,
sex: self.sex,
birthday: self.birthday,
province: value2name(self.address, ChinaAddressV3Data).split(' ')[0],
city: value2name(self.address, ChinaAddressV3Data).split(' ')[1],
area: value2name(self.address, ChinaAddressV3Data).split(' ')[2]
}, data => {
if (data.status === ERROR_OK) {
self.back();
}
});
}
},
back () {
this.$router.go(-1);
}
}
};



