
import { axios } from '@/router/config';
import vheader from '@/components/header/singinHeader';
import Alert from 'vux/src/components/alert/index.vue'

export default {
components: {
vheader,
Alert
},
data () {
return {
username: '',
code: '',
show2: false,
content: ''
};
},
created () {
if (this.queryString('a')) {
this.username = this.queryString('a');
this.code = this.queryString('b');
}
},
methods: {
queryString (name) {
let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
let r = window.location.search.substr(1).match(reg);
if (r !== null) return unescape(r[2]);
return null;
},
active () {
var self = this;
axios('get', '/actives', {
account: self.username,
code: self.code
}, data => {
if (data.status === 200) {
if (data.code === 1 || data.code === 2) {

self.$router.push('/hasActive');
}
} else {
self.show2 = true;
self.content = data.message;
}
});
}
}
};



