import axios from 'axios';
import Cropper from 'cropperjs';
export default {
  components: {},
  data () {
    return {
      headerImage: '',
      picValue: '',
      cropper: '',
      croppable: false,
      panel: false,
      url: ''
    };
  },
  mounted () {
    //初始化这个裁剪框
    var self = this;
    var image = document.getElementById('image');
    this.cropper = new Cropper(image, {
      aspectRatio: 1,
      viewMode: 1,
      background: false,
      zoomable: false,
      ready: function () {
        self.croppable = true;
      }
    });
  },
  methods: {
    getObjectURL (file) {
      var url = null;
      if (window.createObjectURL !== undefined) { //basic
        url = window.createObjectURL(file);
        console.log(url);
      } else if (window.URL !== undefined) { //mozilla(firefox)
        url = window.URL.createObjectURL(file);
      } else if (window.webkitURL !== undefined) { //webkit or chrome
        url = window.webkitURL.createObjectURL(file);
      }
      return url;
    },
    change (e) {
      let files = e.target.files || e.dataTransfer.files;
      if (!files.length) return;
      this.panel = true;
      this.picValue = files[0];
      this.url = this.getObjectURL(this.picValue);
      //每次替换图片要重新得到新的urls
      if (this.cropper) {
        this.cropper.replace(this.url);
      }
      this.panel = true;
    },
    crop () {
      this.panel = false;
      var croppedCanvas;
      var roundedCanvas;

      if (!this.croppable) {
        return;
      }
      //Crop
      croppedCanvas = this.cropper.getCroppedCanvas();
      //Round
      roundedCanvas = this.getRoundedCanvas(croppedCanvas);

      this.headerImage = roundedCanvas.toDataURL();
      this.postImg(this.headerImage);
    },
    getRoundedCanvas (sourceCanvas) {
      var canvas = document.createElement('canvas');
      var context = canvas.getContext('2d');
      var width = sourceCanvas.width;
      var height = sourceCanvas.height;

      canvas.width = width;
      canvas.height = height;

      context.imageSmoothingEnabled = true;
      context.drawImage(sourceCanvas, 0, 0, width, height);
      context.globalCompositeOperation = 'destination-in';
      context.beginPath();
      context.arc(width / 2, height / 2, Math.min(width, height) / 2, 0, 2 * Math.PI, true);
      context.fill();

      return canvas;
    },
    postImg (t) {
      var pic = t.split(',')[1];
      var url = 'http://upload-z1.qiniup.com'; //非华东空间需要根据注意事项 1 修改上传域名
      let param = new FormData(); //创建form对象

      param.append('file', pic);//通过append向form对象添加数据
      param.append('userId', sessionStorage.getItem('userId'));
      console.log('userId' + param.get('userId')); //FormData私有类对象，访问不到，可以通过get判断值是否传进去
      let config = {
        headers: {'Content-Type': 'multipart/form-data'}
      };  //添加请求头
      console.log(this.axios);

      axios.post(url, param, config)
        .then(response => {
          console.log(response.data);
        });
    }
  }
};



