
import { axios } from '@/router/config';
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import CheckIcon from 'vux/src/components/check-icon/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import vheader from '../../components/header/singinHeader';
export default {
components: {
vheader,
XSwitch,
XInput,
XButton,
Group,
CheckIcon,
Cell,
Alert
},
data () {
return {
account: '',
valid1: '',
password: '',
valid4: '',
show2: false,
title: '',
content: '',
demo1: false
};
},
methods: {
getValid1 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^[a-zA-Z0-9]{4,16}$/;
self.valid1 = patt.test(self.account) ? ' ' : '账号必须是4到16位字母或数字组成';
if (self.valid1 === ' ') {
resolve();
}
});
},
getValid4 () {
var self = this;
return new Promise(function (resolve, reject) {
let patt = /^[a-zA-Z0-9]{4,16}$/;
self.valid4 = patt.test(self.password) ? ' ' : '密码必须是4到16位字母或数字组成';
if (self.valid4 === ' ') {
resolve();
}
});
},
login () {
let self = this;
Promise.all([this.getValid1(), this.getValid4()]).then(function (val) {
axios('get', '/login',
{
account: self.account,
password: self.password
}, data => {
if (data.status === 200) {
sessionStorage.setItem('userId', data.data.userId);
sessionStorage.setItem('header', data.data.header);
self.$router.push('/');
} else if (data.status === 1) {
self.$router.push(`/needActive?a=${data.account}`);
} else {
self.show2 = true;

self.content = data.message;
}
});
});
}
}
};



