
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import { axios } from '@/router/config';
import vheader from '@/components/header/singinHeader';
export default {
components: {
vheader,
Group,
Cell,
Alert
},
data () {
return {
username: '',
code: '',
show2: false,
content: '',
block: false
};
},
created () {
if (this.queryString('a')) {
this.username = this.queryString('a');
this.code = this.queryString('b');
}
},
methods: {
queryString (name) {
let reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)');
let r = window.location.search.substr(1).match(reg);
if (r !== null) return unescape(r[2]);
return null;
},
updateEmailCode () {
var self = this;
if (!self.block) {
axios('get', '/updateEmailCode', {
account: self.username
}, data => {
self.show2 = true;
if (data.status === 200) {
self.content = '发送成功，请到填写的邮箱激活，即可立即登陆';
} else {
self.content = data.message;
}
});
}
self.block = true;
setTimeout(() => {
self.block = false;
}, 5000);
}
}
};



