import navList from '../../components/header/navList';

export default {
  data () {
    return {
      active: false,
      header: ''
    };
  },
  components: {
    navList
  },
  created () {
    this.header = sessionStorage.getItem('header');
  },
  methods: {
    showTab () {
      this.active = !this.active;
    }
  }
};



