
const ERROR_OK = 200;
import Cropper from 'cropperjs';
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Badge from 'vux/src/components/badge/index.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import Box from 'vux/src/components/box/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
data () {
return {
url: 'https://o3e85j0cv.qnssl.com/tulips-1083572__340.jpg',
ChinaAddressV3Data: ChinaAddressV3Data,
message: '',
address: '',
headerImage: '',
picValue: '',
cropper: '',
croppable: false,
panel: false,
show: false
};
},
components: {
Cell,
Box,
Badge,
Icon,
Scroller,
Blur,
Flexbox,
FlexboxItem,
Group,
Checker,
CheckerItem,
XInput,
XButton,
Datetime,
XAddress,
ChinaAddressV3Data
},
mounted () {

let self = this;
let image = document.getElementById('image');
self.cropper = new Cropper(image, {
aspectRatio: 1,
viewMode: 1,
background: false,
zoomable: false,
ready: function () {
self.croppable = true;
}
});
self.getUserMessage();
},
methods: {
/*图片*/
getObjectURL (file) {
var url = null;
if (window.createObjectURL !== undefined) { //basic
url = window.createObjectURL(file);
console.log(url);
} else if (window.URL !== undefined) { //mozilla(firefox)
url = window.URL.createObjectURL(file);
} else if (window.webkitURL !== undefined) { //webkit or chrome
url = window.webkitURL.createObjectURL(file);
}
return url;
},
change (e) {
let files = e.target.files || e.dataTransfer.files;
if (!files.length) return;
this.panel = true;
this.picValue = files[0];
this.url = this.getObjectURL(this.picValue);

if (this.cropper) {
this.cropper.replace(this.url);
}
this.panel = true;
},
crop () {
this.panel = false;
var croppedCanvas;
var roundedCanvas;

if (!this.croppable) {
return;
}

croppedCanvas = this.cropper.getCroppedCanvas();

roundedCanvas = this.getRoundedCanvas(croppedCanvas);

this.headerImage = roundedCanvas.toDataURL();
this.postImg(this.headerImage);
},
getRoundedCanvas (sourceCanvas) {
var canvas = document.createElement('canvas');
var context = canvas.getContext('2d');
var width = sourceCanvas.width;
var height = sourceCanvas.height;

canvas.width = width;
canvas.height = height;

context.imageSmoothingEnabled = true;
context.drawImage(sourceCanvas, 0, 0, width, height);
context.globalCompositeOperation = 'destination-in';
context.beginPath();
context.arc(width / 2, height / 2, Math.min(width, height) / 2, 0, 2 * Math.PI, true);
context.fill();

return canvas;
},
/*图片-end*/
postImg (t) {
let pic = t.split(',')[1];
/*let config = {
headers: {'Content-Type': 'multipart/form-data'}
};*/
let formData = new FormData();
formData.append('file', pic);
formData.append('userId', sessionStorage.getItem('userId'));

this.axios('put', '/updateImg', formData, data => {});
},
getUserMessage () {
var self = this;
this.axios('get', '/getUserMessage', {}, data => {
if (data.status === ERROR_OK) {
self.message = data.data;
self.address = `${self.message.province} ${self.message.city} ${self.message.area}`;
self.headerImage = self.message.header.length > 0 ? self.QiNiu + self.message.header : self.message.header;
}
});
},
back () {
this.$router.go(-1);
}
}
};



