
import VueHtml5Editor from 'vue-html5-editor';
const editor = new VueHtml5Editor({
language: 'zh-cn',
hiddenModules: [
'color',
'text',
'font',
'link',
'tabulation',
'info',
'undo',
'hr',
'eraser',
'unlink',
'align',
'list'
],
icons: {
text: 'fa fa-pencil',
color: 'fa fa-paint-brush',
font: 'fa fa-font',
align: 'fa fa-align-justify',
list: 'fa fa-list',
link: 'fa fa-chain',
unlink: 'fa fa-chain-broken',
tabulation: 'fa fa-table',
image: 'fa fa-file-image-o',
hr: 'fa fa-minus',
eraser: 'fa fa-eraser',
undo: 'fa-undo fa',
'full-screen': 'fa fa-arrows-alt',
info: 'fa fa-info'
},
visibleModules: [
'text',
'color',
'font',
'align',
'list',
'link',
'unlink',
'tabulation',
'image',
'hr',
'eraser',
'undo',
'full-screen',
'info'
],


image: {

sizeLimit: 512 * 1024,


upload: {
url: null,
headers: {},
params: {},
fieldName: {}
},



compress: {
width: 1600,
height: 1600,
quality: 80
},


uploadHandler (responseText) {

var json = JSON.parse(responseText);
if (!json.ok) {
alert(json.msg);
} else {
return json.data;
}
}
}
});
import XHeader from 'vux/src/components/x-header/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Blur from 'vux/src/components/blur/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV3Data from 'vux/src/datas/china_address_v3.json'
import Box from 'vux/src/components/box/index.vue'
import Icon from 'vux/src/components/icon/index.vue'

export default {
components: {
XHeader,
Scroller,
Cell,
Box,
Badge,
Icon,
Blur,
Group,
Checker,
CheckerItem,
XInput,
XButton,
Datetime,
XAddress,
ChinaAddressV3Data,
editor
},
data () {
return {
txtHeight: function () {
let $height = document.body.clientHeight;
$height = $height - 46 * 3 - 37;//3个group和一个padding
return $height;
},
content: 'Hello World!'
};
},
mounted () {
console.log(this.txtHeight());
},
methods: {
updateData: function (data) {

this.content = data;
}
}
};



